import React from "react";
import OrderDetailsStyles from "./order-details.module.css";
import PropTypes from 'prop-types';
import doneImagePath from "../../images/done.svg";
import { useSelector } from "react-redux";
import loader from '../../images/loading.png';

const OrderDetails = () => {
  const {orderNumber, hasError, isLoading} = useSelector(store => store.order)
  if (isLoading) {
    return (
      <>
        <p className={`text text_type_main-medium mt-4 ${OrderDetailsStyles.text}`}>, </p>
        <p className={`text text_type_main-medium mt-2 ${OrderDetailsStyles.text}`}>  </p>
        <img className={OrderDetailsStyles.loader} src={loader} alt='Loading...'/>
        <p className={`text text_type_main-small mb-8 ${OrderDetailsStyles.text}`}>  15-20 </p>
      </>
    )
  }
  
  return (
    <>
    { !hasError ?
    <div className={OrderDetailsStyles.order}>
      <h4 className="text text_type_digits-large mb-8">{orderNumber}</h4>
      <span className="text text_type_main-medium"> </span>
      <img className={OrderDetailsStyles.doneImage} src={doneImagePath} alt="done"/>
      <p className="text text_type_main-small mb-2">   </p>
      <p className="text text_type_main-small text_color_inactive">    </p>
    </div>
      :
    <p className={`text text_type_main-medium mb-8 ${OrderDetailsStyles.text}`}>-   </p>
    }
    </>
  )
}

OrderDetails.propTypes = {
  orderNumber: PropTypes.number
}

export default OrderDetails;